.wrapper {
    display: flex;
    flex-direction: column;
    width: 140px;
}

.container {
    display: flex;
    flex-direction: column;
}
.container a {
    text-decoration: none;
    color: var(--text);
    padding: 8px;
    border-radius: 4px;
}

.container a:hover {
    background: #e639461f;
}

.container a.active {
    color: var(--primary);
}